import React, { useState } from 'react';
import axios from 'axios';

import Header from './Header';
import Footer from './Footer';
import FriendsList from './FriendsList';
import Chats from './Chats';
import Settings from './Settings';

import './sidebar.scss';

export default () => {
  const [activeTab, setActiveTab] = useState('Settings');
  const [friends, setFriends] = useState([]);
  const getFriends = async () => {
    const { data: friends } = await axios.get('https://api.shankar.xyz/users', {
      headers: {
        Authorization: localStorage.getItem('TOKEN')
      }
    });
    setFriends(friends);
  }
  if (!friends.length) getFriends();

  const logout = () => {
    localStorage.clear();
    window.location.reload();
  };

  return <div className="sidebar">
    <Header activeTab={activeTab}/>
    <div className="sidebar-dynamic-content">
      {
        activeTab === 'Contacts' && <FriendsList friends={friends} />
      }
      {
        activeTab === 'Chats' && <Chats />
      }
      {
        activeTab === 'Settings' && <Settings logout={logout}/>
      }
    </div>
    <Footer setActiveTab={setActiveTab} activeTab={activeTab} />
  </div>
}
